import Link from 'next/link'

function Footer() {
  return (
    <footer style={navStyle}>

      <div>
        <h4 style={linkStyle}>
            Author
        </h4>
        <p>Daniel Aduro</p>
        <h3 style={linkStyle}>
            Version
        </h3>
        <p>1.0.0</p>
        <h3 style={linkStyle}>
            Copyright
        </h3>
        <p> &copy; 2019 </p>
        <h3 style={linkStyle}>
            About App
        </h3>

      <Link href='/'>
        <a style={linkStyle}>Home</a>
      </Link>
      <Link href='/about'>
        <a style={linkStyle}>About</a>
      </Link>
      </div>

    </footer>
  )
}

const linkStyle = {
    textDecoration: 'none',
    weight: 'bold',
    color: '#ffffff',
    marginRight: '15px',
  }
  
  const navStyle = {
    background: '#05052b',
    position: 'fixed',
    bottom: '0',
    width: '100%',
    height: '80px',
    lineHeight: '80px',
    padding: '0 20px',
    textAlign: 'right',
    padding: '10px',
    color: '#ffffff',
  }

export default Footer;